import React from "react";
import { NavLink } from "react-router-dom";

const Common = (props) => {
    return (
        <>
            <section id="header" className="d-flex align-items-center">
             <div className='container-fluid nav_bg'>
              <div className='row'>
                 <div className='col-10 mx-auto'>
                 <div className="row">
                    <div className="col-lg-6 col-md-6 col-12 col-xxl-6 d-flex justify-content-center flex-column">
                        <h1>{props.name} <strong className="brand-name">StellarDesigner</strong></h1>
                        <h2 className="my-3">
                            We are the thing what you looking for.
                        </h2>
                        <div className="mt-3">
                            <NavLink to={props.visit}  className="btn-get-started"> {props.btname}</NavLink>
                        </div>
                    </div>
                    <div className="col-lg-6 col-md-6 col-12 col-xxl-6 header-img">
                        <img src={props.imgsrc} className="img-fluid img-edit" alt="image"/>
                    </div>
                    </div>
                 </div>
               </div>
             </div>
            </section>
        </>
    )
}
export default Common;