import React from "react";
import web from "../src/images/img1.svg";
import Common from "./Common";
import varun from "../src/images/founder.JPEG";
import Card  from "./Card";
import Sdata from "./Sdata";

const About = () => {
    return (
        <>
            <Common
                name="Welcome to About page"
                imgsrc={web}
                visit="#about-main"
                btname="Know Us"
            />
            <section className="founder-cont my-3">
                <div className="display-4 text-center">
                    <h1>Founder</h1>
                    <hr className="w-25 mx-auto color-hr"/>
                </div>

                <div className="container">
                    <div className="row my-5">
                        <div className="col-lg-6 col-md-6 col-12 col-xxl-6">
                           <figure>
                               <img className="img-fluid founder-img" src={varun} alt="Founder-image"/>
                           </figure>
                        </div>
                        <div className="col-lg-6 col-md-6 col-12 col-xxl-6 d-flex justify-content-center flex-column">
                            <h2>Meet Varun,</h2>
                            <br></br>
                            <p>I believe in startups and strongly believed that without websites, it's impossible to take our business to next level. The online world can give you or your business a greater amount of consumers which you can't imagine in the physical world.
                             <br/> <br/>
                           I started the stellar designer with the motive of providing the finest services ever you can get from anywhere.I am sure you will get a great experience from us.</p>
                        </div>
                    </div>
                </div>
            </section>
            <div className="my-5">
             <h1 className="text-center">Our Services</h1>
             <hr className="w-25 mx-auto color-hr"/>

             <div className='container-fluid nav_bg'>
               <div className='row'>
                 <div className='col-10 mx-auto'>
                   <div className="row my-2 gy-4">
                       {
                           Sdata.map((val, ind) =>{
                               return <Card
                                   key={ind}
                                   imgsrc={val.imgsrc}
                                   title={val.title}
                                   button={val.button}
                                   description={val.description}
                               />
                           })
                       }
                   </div>
                 </div>
               </div>
             </div>
         </div>
        </>
    )
}
export default About;