import React from "react";
import { NavLink } from "react-router-dom";
// import "./index.css";
const Card = (props) => {
    return (

        <>
           <div className="col-md-4 col-10 mx-auto">
            <div className="card ">
                <img src={props.imgsrc} className="card-img-top card-height" alt={props.imgsrc} />
                <div className="card-body">
                    <h5 className="card-title fw-bold">{props.title}</h5>
                    <p className="card-text">{props.description}</p>
                    <NavLink to="/Contact" className="btn-get-started">{props.button}</NavLink>
                </div>
                </div>
            </div>
                        
        </>
    )
}
export default Card;